@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;700&display=swap');

body {
    background-color: azure;
}

.chart-card[data-type*="homepage"], .chart-card[data-type*="advertising"], .chart-card[data-type*="gmb-reviews"] {
    font-family: 'Montserrat', sans-serif;
    position: relative;
    margin: 10px;
    background-color: #fdfdfd;
    width: 100%;
    height: auto;
    min-height: 100px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;


    .chart-card__header {
        overflow: hidden;
        margin-top: 0;
        width: 100%;
        height: auto;
        position: relative;
        min-height: 30px;
        border-radius: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        padding: 15px;
        background-color: #a1a1a1;
        transition: 0.5s all ease-out;

        .chart-card__header__title {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            gap: 15px;
            color: white;

            h3 {
                margin: 0;
                font-size: 20px;
                font-weight: 700;
            }
            span {
                background-color: white;
                border-radius: 10px;
                color: black;
                padding: 2px 10px;
                font-size: 11px;
                font-weight: 400;
            }
        }

        .chart-card__header__round {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: center;
            gap: 10px;
            color: white;

            span {
                color: white;
                font-size: 19px;
                font-weight: 400;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }


    .red {
        background-color: red;
    }
    .green {
        background-color: green;
    }
    .yellow {
        background-color: yellow;
    }
    .orange {
        background-color: orange;
    }

    .chart-card__content {
        display: flex;
        flex-direction: column;
        gap: 5px;
        
        & .content__row {
            margin: 10px 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 15px;
            font-size: 18px;

            & .text {
                width: 50%;
                font-weight: 400;
                
                padding: 0px 20px;
            }


            & .bar {
                width: 30%;
                text-align: right;
                position: relative;

                & .bar__background {
                    display: block;
                    position: relative;
                    width: 100%;
                    height: 4px;
                    background-color: #efefef;

                    & .bar__content {
                        position: absolute;
                        top: 0;
                        left: 0;
                        height: 4px;
                        width: 0;
                        transition: all 1s ease-out;
                    }
                    
                }
            }

            & .perc {
                width: 20%;
            }
            .red-text {
                color: red;
            }
            .green-text {
                color: green;
            }
            .yellow-text {
                color: yellow;
            }
            .orange-text { 
                color: orange;
            }
        }
    }

    .chart-card__footer {
        display: flex;
        align-items: center;
        justify-content: center;
        height: 80px;
        margin: 0 10px;
        border-top: grey dashed 1px;

        & button {
            background-color: red;
            outline: 0;
            border: 0;
            border-radius: 20px;
            color: white;
            font-size: 12px;
            padding: 6px 15px;

            &:hover {
                background-color: black;
            }
        }
    }
}

.loading {
    .chart-card__header::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        animation: 1s loading-pulse alternate;
        animation-iteration-count: infinite;
        z-index: 9999;
    }
    .chart-card__content .content__row .bar::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        animation: 1s loading-pulse alternate;
        animation-iteration-count: infinite;
        z-index: 9999;
    }
}

